import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我的订单' //导航栏文字
			},
			tabIndex: 0, //订单状态索引
			tabList: [{
				text: '全部',
				val: ''
			},{
				text: '待支付',
				val: '0'
			},{
				text: '待发货',
				val: '20'
			},{
				text: '待收货',
				val: '30'
			},{
				text: '售后',
				val: '70,80,40'
			}],
			// ,{
			// 	text: '已完成',
			// 	val: '40'
			// }
			params: {
				page: 1, //页数
				size: 10, //每页几条
				status: ''
			}, //分页参数
			orderPageData: {}, //订单数据
			orderPageList: [], //订单列表
			orderCount: [] //每个订单的商品个数
		}
	},
	onLoad(options) {
		console.log('options')
		this.tabIndex = options.tabIndex
		this.params.status = options.tabIndex == 0 ? '' : parseInt(options.tabIndex) - 1 // this.tabList[this.tabIndex].val
	},
	onShow() {
		this.initData()
	},
	onReachBottom() {
		if (this.orderPageList.length >= this.orderPageData.total) {
			return
		}
		this.params.page++;
		this.getOrderPageList()
	},
	methods: {
		initData() {
			//重置分页参数
			this.orderPageData = {}
			this.orderPageList = []
			this.params.page = 1
			this.getOrderPageList();
		},
		//查询订单列表
		getOrderPageList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.orderPageList, this.params).then((res) => {
				that.orderPageData = res.data
				if(that.params.page == 1) {
					that.orderPageList = res.data.data
				} else {
					that.orderPageList = that.orderPageList.concat(res.data.data)
				}
				that.orderCount = []
				let num = 0;
				for(var item of that.orderPageList) {
					for(var subItem of item.cartList) {
						num += subItem.quantity
						item.statusText = this.setStatus(item.status)
					}
					that.orderCount.push(num)
					num = 0;
				}
			});
		},
		setStatus(status) {
			if(status == 0) {
				return '待支付'
			} else if(status == 1) {
				return '待发货'
			} else if(status == 2) {
				return '待收货'
			} else if(status == 40) {
				return '已完成'
			} else if(status == 3) {
				return '售后'
			}
		},
		selectTab(index, status) {
			this.tabIndex = index
			this.params.status = index == 0 ? '' : parseInt(index) - 1
			this.initData();
		},
		//删除订单(暂无)
		delOrder() {
			let that = this
			interfaceurl.checkAuth(interfaceurl.xxx, {}).then((res) => {
				
			});
		},
		//确认支付
		confirmPay(item) {
			let that = this
			let params = {
				cartId: item.cartIds,
				addressId: item.addressId,
				invoiceType: 1, //发票类型:普通发票
				invoiceContent: 0, //发票内容
				invoiceHeadType: '', //个人，公司
				invoiceHeadName: '', //invoiceHeadType = 1 必填姓名
				invoiceHeadCompanyName: '', //invoiceHeadType =2 必填公司名称
				registeredAddress: '', //注册地址
				workPhone: '', //单位电话
				bank: '', //开户银行
				bankAccounts: '' //银行账户
			}
			uni.showLoading()
			interfaceurl.checkAuth(interfaceurl.orderPayment, params).then((res) => {
				uni.requestPayment({
					timeStamp: res.data.timeStamp,
					nonceStr: res.data.nonceStr,
					package: res.data.package,
					signType: res.data.signType,
					paySign: res.data.paySign,
					success: (res) => {
						that.initData()
						uni.hideLoading()
					},
					fail: (res) => {
						uni.hideLoading()
						uni.showToast({
						    title: '支付失败',
						    icon: 'none',
						    duration: 2000
						});
					},
					complete: () => {
						
					}
				});
			});
		},
		//申请退款(取消订单)
		applyRefund(orderId) {
			let that = this
			let params = {
				orderId: orderId,
				reason: '' //申请原因
			}
			interfaceurl.checkAuth(interfaceurl.orderApplyRefund, params).then((res) => {
				uni.showToast({
				    title: '申请退款成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		//确认收货
		confirmTake(orderId) {
			let that = this
			interfaceurl.checkAuth(interfaceurl.orderConfirm, {orderId: orderId}).then((res) => {
				uni.showToast({
				    title: '确认收货成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		//查看物流
		queryFlow(item) {
			this.$turnPage('/pages/shopping/trade/order-logistics', 'navigateTo')
			// item.orderChildList.orderId
			// //跳转物流页面
			// let that = this
			// interfaceurl.checkAuth(interfaceurl.orderExpressInfo, {}).then((res) => {
				
			// });
		},
		//申请退货退款
		applySalesRefund(item) {
			let that = this
			let params = {
				orderId: item.item.noId,
				reason: '',
				code: '', //申请的sku(用户购买的东西)
				type: 0 //0：退货退款，1：仅退款
			}
			interfaceurl.checkAuth(interfaceurl.orderReturnRefund, params).then((res) => {
				uni.showToast({
				    title: '申请退货退款成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		//取消退货退款
		cancelSalesRefund(item) {
			let that = this
			let params = {
				orderId: '',
				reason: '',
				code: '', //申请的sku(用户购买的东西)
				type: 0 //0：退货退款，1：仅退款
			}
			interfaceurl.checkAuth(interfaceurl.orderCancelApply, params).then((res) => {
				uni.showToast({
				    title: '取消退货退款成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		}
	}
}
